<template>
  <div>
    <v-row class="ma-0 pa-0 blue lighten-5">
      <v-col cols="12" md="5" class="ma-0 mt-2 pa-1">
        <v-text-field
          type="date"
          v-model="desde"
          :color="color_salud"
          label="Desde"
          dense
          outlined
          background-color="blue lighten-4"
        ></v-text-field>
      </v-col>
      <v-col cols="10" md="5" class="ma-0 mt-2 pa-1">
        <v-text-field
          type="date"
          v-model="hasta"
          :color="color_salud"
          label="Hasta"
          dense
          outlined
          background-color="blue lighten-4"
        ></v-text-field>
      </v-col>
      <v-col cols="2" md="2" class="ma-0 mt-2 pa-1">
        <v-btn
          class="py-1"
          block
          dark
          color="blue darken-4"
          @click="listarResultados()"
        >
          <v-icon> search </v-icon>
        </v-btn>
      </v-col>
    </v-row>
    <v-row class="ma-0 pa-0 blue lighten-5">
      <v-col cols="12" class="ma-0 pa-0">
        <p class="font-weight-bold">Nivel de Urgencia:</p>
      </v-col>
      <v-col cols="4" class="ma-0 pa-0">
        <v-icon color="red" dense> message </v-icon>
        Alta
      </v-col>
      <v-col cols="4" class="ma-0 pa-0">
        <v-icon color="orange" dense> message </v-icon>
        Medio
      </v-col>
      <v-col cols="4" class="ma-0 pa-0">
        <v-icon color="blue" dense> message </v-icon>
        Normal
      </v-col>
    </v-row>
    <v-data-table
      dense
      fixed-header
      :headers="dataHeaders"
      :items="dataTablaResultado"
      :search="search"
      class="elevation-1 table_main font-weight-medium"
      :loading="loading"
      loading-text="Cargando.. Espere porfavor"
    >
      <template v-slot:[`item.nombres`]="{ item }">
        {{ item.nombres }} {{ item.apellidos }}
      </template>
      <template v-slot:[`item.fechaInforme`]="{ item }">
        {{ convertidorFecha(item.fechaInforme) }}
      </template>
      <template v-slot:[`item.horaInforme`]="{ item }">
        {{ item.horaInforme }}
      </template>
      <template v-slot:[`item.comentario`]="{ item }">
        <v-btn
          outlined
          @click="
            () => {
              (dialogComentario = true), (comentario = item.descripcion);
            }
          "
          elevation="3"
          icon
        >
          <v-icon
            :color="
              item.nivelUrgencia == 'M'
                ? 'orange'
                : item.nivelUrgencia == 'A'
                ? 'red'
                : 'blue'
            "
            >speaker_notes</v-icon
          >
        </v-btn>
      </template>
      <template v-slot:[`item.descarga`]="{ item }">
        <v-btn
          outlined
          @click="descargarPDF(item.idResultado)"
          elevation="3"
          icon
        >
          <v-icon>get_app</v-icon>
        </v-btn>
      </template>
      <template v-slot:[`item.eliminar`]="{ item }">
        <v-btn
          outlined
          @click="
            showDialogDeleteResultado(
              item.examen,
              item.nombres + ' ' + item.apellidos,
              item.idExamen
            )
          "
          elevation="3"
          icon
        >
          <v-icon>minimize</v-icon>
        </v-btn>
      </template>
      <template v-slot:[`item.notificar`]="{ item }">
        <v-btn outlined @click="notificar(item)" elevation="3" icon>
          <v-progress-circular
            v-if="item.estadoNotificacion == 'load'"
            indeterminate
            color="primary"
          ></v-progress-circular>

          <v-icon v-if="item.estadoNotificacion == 'bell'"
            >notifications</v-icon
          >
          <v-icon v-if="item.estadoNotificacion == 'error'" color="#C62828"
            >error</v-icon
          >
          <v-icon v-if="item.estadoNotificacion == 'check'" color="#00C853"
            >check</v-icon
          >
        </v-btn>
      </template>
    </v-data-table>

    <v-dialog v-model="dialogComentario" max-width="290">
      <v-card class="py-2">
        <v-list-item-content class="text-center">
          <div class="font-weight-bold">Comentario</div>
        </v-list-item-content>
        <v-list-item-content>
          <div class="text-center">
            {{ comentario == "" ? "SIN MENSAJE" : comentario }}
          </div>
        </v-list-item-content>
        <v-list-item-content>
          <v-layout mx-2 :justify-center="true">
            <v-btn
              @click="
                () => {
                  (dialogComentario = false), (comentario = '');
                }
              "
              dark
              >Cerrar</v-btn
            >
          </v-layout>
        </v-list-item-content>
      </v-card>
    </v-dialog>
    <v-dialog v-model="dialogDeleteResultado" max-width="290" persistent>
      <v-card class="py-2">
        <v-list-item-content class="text-center">
          <div class="font-weight-bold">
            Eliminar
            <span :class="color_letras_salud" class="text-center ma-2">
              {{ dataDialogDelResultado.examen }}
            </span>
          </div>
          <div class="font-weight-bold">
            de
            <span :class="color_letras_salud" class="text-center ma-2">
              {{ dataDialogDelResultado.nombres }}
            </span>
          </div>
        </v-list-item-content>
        <v-list-item-content>
          <v-layout mx-2 :justify-space-between="true">
            <v-btn
              @click="eliminar(dataDialogDelResultado.idExamen)"
              rounded
              outlined
              >Si</v-btn
            >
            <v-btn @click="notShowDialogDeleteResultado" rounded dark>No</v-btn>
          </v-layout>
        </v-list-item-content>
      </v-card>
    </v-dialog>
  </div>
</template>
<script>
import JSPDF from "jspdf";
import { mapState } from "vuex";
export default {
  name: "ListaExamenesHoy",
  data() {
    return {
      notificacionbell: true,
      notificacionLoad: false,
      notificacionErrada: false,
      notificacionRealizada: false,
      color_salud: "blue darken-3",
      color_letras_salud: "blue-grey--text text-darken-4",
      desde: "",
      hasta: "",
      search: "",
      dataHeaders: [
        {
          text: "*",
          align: "start",
          sortable: false,
          value: "index",
          class:
            "headerStyleLeft blue darken-2 subtitle-1 text-center  font-weight-bold white--text ",
        },
        {
          text: "Paciente",
          align: "center",
          sortable: false,
          value: "nombres",
          class: "blue darken-2  subtitle-1  font-weight-bold white--text",
        },
        {
          text: "Examen",
          align: "center",
          value: "examen",
          sortable: false,
          class: "blue darken-2 subtitle-1   font-weight-bold white--text",
        },
        {
          text: "Fecha",
          align: "center",
          value: "fechaInforme",
          class:
            "headerStyleRigtht blue darken-2  subtitle-1  font-weight-bold white--text",
        },
        {
          text: "Hora",
          align: "center",
          value: "horaInforme",
          class:
            "headerStyleRigtht blue darken-2  subtitle-1 font-weight-bold white--text",
        },
        {
          text: "",
          value: "comentario",
          class:
            "headerStyleRigtht blue darken-2  font-weight-bold white--text",
        },
        {
          text: "",
          value: "descarga",
          class:
            "headerStyleRigtht blue darken-2   font-weight-bold white--text",
        },
        {
          text: "",
          sortable: false,
          value: "eliminar",
          class:
            "headerStyleRigtht blue darken-2   font-weight-bold white--text",
        },
        {
          text: "",
          sortable: false,
          value: "notificar",
          class:
            "headerStyleRigtht blue darken-2  font-weight-bold white--text",
        },
      ],
      comentario: "",
      dialogComentario: false,
      dataTablaResultado: [],
      dataDialogDelResultado: [],
      dialogDeleteResultado: false,
      loading: "",
    };
  },
  computed: {
    ...mapState(["token"]),
  },
  async mounted() {
    await this.inicializarFechas();
    this.listarResultados();
  },
  methods: {
    inicializarFechas() {
      var hoy = new Date();
      var año = hoy.getFullYear();
      var mes = hoy.getMonth() + 1;
      mes = mes < 10 ? "0" + mes : mes;
      var dia = hoy.getDate();
      dia = dia < 10 ? "0" + dia : dia;

      this.hasta = año + "-" + mes + "-" + dia;
      hoy.setDate(hoy.getDate() - 7);
      año = hoy.getFullYear();
      mes = hoy.getMonth() + 1;
      mes = mes < 10 ? "0" + mes : mes;
      dia = hoy.getDate();
      dia = dia < 10 ? "0" + dia : dia;
      this.desde = año + "-" + mes + "-" + dia;
    },
    showDialogDeleteResultado(examen, nombres, idExamen) {
      this.dataDialogDelResultado.nombres = nombres;
      this.dataDialogDelResultado.examen = examen;
      this.dataDialogDelResultado.idExamen = idExamen;
      this.dialogDeleteResultado = true;
    },
    notShowDialogDeleteResultado() {
      this.dataDialogDelResultado.nombres = null;
      this.dataDialogDelResultado.examen = null;
      this.dataDialogDelResultado.idExamen = null;
      this.dialogDeleteResultado = false;
    },
    listarResultados() {
      this.loading = true;
      var headers = {
        "Content-Type": "application/json",
        token: this.token,
      };

      this.axios
        .post(
          "/listarResultados",
          { fechaDesde: this.desde, fechaHasta: this.hasta },
          { headers }
        )
        .then((res) => {
          let index = 0;
          res.data = res.data.filter((e) => {
            index++;
            e.index = index;
            e.estadoNotificacion = "bell";
            return e;
          });

          this.dataTablaResultado = res.data;

          // //Como laconsulta delresultado no trae el nombre del examen ,
          // //Sacamos el nombre de las listas de los examenes de acuerdo al IdExamen
          //        this.dataTablaResultado.map(e=>{
          //             recorrer:
          //             for (let index = 0; index < dataNombreExamen.length; index++) {
          //                 if(e.idExamen==dataNombreExamen[index].idExamen){
          //                     e.examen=dataNombreExamen[index].examen
          //                     break recorrer;
          //                 }
          //             }
          //             return e
          //     })
          this.loading = false;
        })
        .catch((err) => {
          this.loading = false;
          console.log(err);
        });
    },
    generarPDF() {
      this.ruta = "";
      const doc = new JSPDF();

      doc.setFillColor(0, 0, 255);
      doc.ellipse(10, 5, 10, 5, "F");
      doc.setLineWidth(1);
      doc.setDrawColor(0);
      doc.setFillColor(255, 0, 0);
      doc.circle(120, 20, 5, "FD");
      //doc.save('test.pdf');
      this.ruta = doc.output("datauri");

      console.log(this.ruta);
      this.ruta = "/" + this.ruta.substring(this.ruta.indexOf("base64,") + 7);
      console.log(this.ruta);
      //   console.log(this.$route.query.page);
      //    this.$refs.modal_pdf.click()
      window.open(doc.output("bloburl"), "_blank");
    },
    convertidorFecha(str) {
      if (str == null) {
        return "Sin Data";
      } else {
        const f = new Date(str);
        var año = f.getFullYear();
        var mes = f.getMonth() + 1;
        mes = mes < 10 ? "0" + mes : mes;
        var dia = f.getDate();
        dia = dia < 10 ? "0" + dia : dia;
        return año + "/" + mes + "/" + dia;
      }
    },
    verPDF(idPDF) {
      this.axios
        .post("/getFile", { id: idPDF })
        .then((res) => {
          var arrayBuffer = res.data.pdf.data;
          var array = new Uint8Array(arrayBuffer);
          var blob = new Blob([array], { type: "application/pdf" });
          const blobUrl = URL.createObjectURL(blob);
          window.open(blobUrl, "_blank");
        })
        .catch((err) => {
          console.log(err);
        });
    },
    descargarPDF(idPDF) {
      this.axios
        .post("/getFile", { id: idPDF })
        .then((res) => {
          var nombreArchivo = res.data.nombrePdf;
          var arrayBuffer = res.data.pdf.data;
          var array = new Uint8Array(arrayBuffer);
          var blob = new Blob([array], { type: "application/pdf" });
          const blobUrl = URL.createObjectURL(blob);
          var downloadLink = document.createElement("a");
          downloadLink.href = blobUrl;
          downloadLink.download = nombreArchivo;
          document.body.appendChild(downloadLink);
          downloadLink.click();
          document.body.removeChild(downloadLink);
        })
        .catch((err) => {
          console.log(err);
        });
    },
    eliminar(id) {
      var headers = {
        "Content-Type": "application/json",
        token: this.token,
      };
      this.axios
        .post("/cambiarVisibilidadResultado", { idExamen: id }, { headers })
        .then((res) => {
          this.dataTablaResultado = this.dataTablaResultado.filter((e) => {
            if (e.idExamen != id) return e;
          });
          this.dialogDeleteResultado = false;
        })
        .catch((err) => console.log(err));
    },
    notificar(item) {
      this.notificacionbell = false;
      this.notificacionLoad = true;
      this.dataTablaResultado.map((e) => {
        if (e.index == item.index) {
          return (e.estadoNotificacion = "load");
        }
      });

      var headers = {
        "Content-Type": "application/json",
        token: this.token,
      };

      this.axios
        .post(
          "/notificar",
          {
            examen: item.examen,
            nombre: item.nombres + " " + item.apellidos,
            telefono: item.telefono,
          },
          { headers }
        )
        .then((res) => {
          console.log(res.data);
          this.dataTablaResultado.map((e) => {
            if (e.index == item.index) {
              return (e.estadoNotificacion = "check");
            }
          });
          setTimeout(() => {
            this.dataTablaResultado.map((e) => {
              if (e.index == item.index) {
                return (e.estadoNotificacion = "bell");
              }
            });
          }, 2000);
        })
        .catch((err) => {
           this.dataTablaResultado.map((e) => {
            if (e.index == item.index) {
              return (e.estadoNotificacion = "error");
            }
          });
          setTimeout(() => {
            this.dataTablaResultado.map((e) => {
              if (e.index == item.index) {
                return (e.estadoNotificacion = "bell");
              }
            });
          }, 2000);

          console.log(err);
        });
    },
  },
};
</script>
<style >
::-webkit-scrollbar {
  width: 5px;
}
::-webkit-scrollbar-thumb {
  background: #1565c0;
  border-radius: 10px;
}
::-webkit-scrollbar-thumb:hover {
  background: #1565c0;
}
.table_main {
  border: 2px solid #1976d2;
}
</style>