                                      v-for="element in Clasificador(item.estado)" :key="element.nro" >
                                        <div class="col-12" :class="{'green accent-4': element.idResultado!=null}" >
                                            <div  class="d-flex justify-space-between  font-weight-bold">
                                                    <span  :class="element.estado==='atrasado' && element.idResultado!=null?'':'red--text text-darken-4' " class=" " ><v-icon class="mx-1" color="red accent-4">business</v-icon>{{element.examen.toUpperCase()}}</span>
                                                    <span class="">{{convertidorFecha(element.fechaEntregaResultado)}}</span>
                                            </div>
                                            <div class="d-flex font-weight-bold"> {{element.nombres.toUpperCase()}} {{element.apellidos.toUpperCase()}}</div>
