            </v-row>
              <br>
         <v-row class="ma-0 pa-0" >
            <v-col sm="10" md="10" lg="11"  class="mt-4 px-0 mx-auto card_trazabilidad">
                <v-card class="text-center "   >
                <v-card-title class="justify-center "  >
                        <h3 >Trazabilidad de Exámenes</h3>
                 </v-card-title>  
    <v-form ref="formularioFiltroTrazabilidad"   lazy-validation>
                  <v-row class="blue lighten-5 ma-0 pa-0" >
                        <v-col sm="10" md="4" >
                                    <v-select 
                                    :items="filtroTrazabilidad" 
                                    :item-color="color_salud" 
                                    :class="color_text_salud" 
                                    label="Filtro" 
                                    :color="color_salud" 
                                    background-color="blue lighten-4"  
                                    item-text="descripcion"
                                    item-value="valueFiltro"
                                    v-model="modelFiltroTrazabilidad"
                                    dense 
                                    outlined ></v-select>
                            </v-col>
                            <v-col sm="10" md="3" >
                                <v-btn class="px-5" block dark color="blue darken-4" @click="listarPorFiltro()">Buscar</v-btn>
                            </v-col>
                    </v-row>       
    </v-form>
                 
                        <v-data-table
                        dense
                        fixed-header  
                        :headers="dataHeaders"
                        :items="dataListTrazabilidadExamenes"
                        :search="search"
                        class="elevation-1 table_main font-weight-medium"
                        :loading="loading"
                        loading-text="Cargando.. Espere porfavor"
                        >
                        
                        <template v-slot:[`item.nombres`]="{ item }">
                                    {{item.nombres}} {{item.apellidos}}
                        </template>
                        <template v-slot:[`item.fechaEntregaResultado`]="{ item }">
                                {{ convertidorFecha(item.fechaEntregaResultado) }}
                        </template>
                        <template  v-slot:[`item.fechaInforme`]="{ item }">
                            <div   v-if="convertidorFecha(item.fechaInforme).length>8">
                                <v-chip color="green" dark>   {{convertidorFecha(item.fechaInforme)}}</v-chip>
                            </div> 
                            <div v-else>
                                <v-chip color="red" dark> No Recepcionado </v-chip>      
                        </div>
                        </template>           
                        </v-data-table>
                </v-card>
            </v-col>       
        </v-row>
        
    </v-container>
