<script>
import Listahoy from '../components/ListaExamenesHoy'
import {mapState} from 'vuex'
export default {
    nama:'Trazabilidad',
    components:{Listahoy},
            data() {
                return {
                    color_salud:'blue darken-3',
                    color_text_salud:'blue--text text-lighten-4',
                    requeridoRules: [(v) => !!v || "es requerido"],
                    url:'',
                    offsetTop: 0,
                    TabsExam:null,
                    archivo:null,
                    descripcion:'',
                    dataListExamenes:[
                    ],
                    dataListTrazabilidadExamenes:[
                    ],
                // EstadoExamen:[{estado:'atrasado'},{estado:'hoy'},{estado:'pendiente'}],
                search:'',
                dataHeaders:[
                 { text:'*', align: 'start', sortable: false, value: 'index', class:"headerStyleLeft blue darken-3 subtitle-1 text-center  font-weight-bold white--text " },
                 { text:'Examen', align: 'center', value: 'examen', sortable: false, class:" blue darken-3  subtitle-1   font-weight-bold white--text" },
                 { text:'Paciente', align: 'center', sortable: false, value: 'nombres',class:" blue darken-3   subtitle-1  font-weight-bold white--text" },
                 { text:'Fecha Limite', align: 'center', value: 'fechaEntregaResultado', class:"headerStyleRigtht blue darken-3   subtitle-1  font-weight-bold white--text" }, 
                 { text:'Fecha Llegada', align: 'center', value: 'fechaInforme', class:"headerStyleRigtht blue darken-3   subtitle-1  font-weight-bold white--text" }, 
                 { text:'',  sortable: false, value: 'estado' ,class:"headerStyleRigtht blue darken-3   font-weight-bold white--text" },          
                 ],
                 modelFiltroTrazabilidad:'Hoy',
                 filtroTrazabilidad:[
                     {descripcion:'Hoy',valueFiltro:'Hoy'},
                     {descripcion:'Atrasado',valueFiltro:'Atrasado'},
                     {descripcion:'Pendiente',valueFiltro:'Pendiente'}
                ],
                 loading:true
                }
            },
            mounted() {
                // this.Clasificador();
                // this.listarExamenes();
                this.listarExamenesHoy();
            },
            computed: {
                ...mapState(['token'])
            },
            methods: { 
            //    listarExamenes(){
            //        this.dataListExamenes=[]
            //          var headers =  {
            //             "Content-Type": "application/json",
            //             token: this.token,
            //         };
                       
            //        this.axios.get('/listaCompletaDePendientesAdmision',{headers})
            //        .then(res=>{
            //                     this.dataListExamenes.push(...res.data);
            //                     this.dataListExamenes.map(e=> {   var cantidad =this.restarFecha(this.convertidorFecha(e.fechaEntregaResultado));
            //                              e.estado= cantidad >0 ?'pendiente':(cantidad==0?'hoy':'atrasado')
            //                              return e;      });
            //        })                  
            //       },
                    listarPorFiltro(){
                    this.dataListTrazabilidadExamenes=[];
                    var headers =  {
                        "Content-Type": "application/json",
                        token: this.token,
                      };
                      if(this.modelFiltroTrazabilidad=='Hoy'){
                          this.listarExamenesHoy();
                      }else if(this.modelFiltroTrazabilidad=='Atrasado'){
                          this.listarExamenesAtrasados(headers)
                      }else{
                          this.listarExamenesPendientes(headers)
                      }
                      
                  },
                  listarExamenesHoy(){
                       var headers =  {
                        "Content-Type": "application/json",
                        token: this.token,
                      };
                     this.axios.get('/listaCompletaDeHoyAdmision',{headers})
                    .then(res=>{
                        this.dataListTrazabilidadExamenes.push(...res.data);
                        var i=0;
                        this.dataListTrazabilidadExamenes.map(e=>{
                            i++
                            e.index=i;
                            return e;
                        })
                        this.loading=false
                    }).catch(err=>this.loading=false )

                  },
                  listarExamenesAtrasados(headers){      
                    var valido=  this.$refs.formularioFiltroTrazabilidad.validate();            
                    
                    if(valido){
                           this.axios.get('/listaCompletaDeAtrasadosAdmision',{headers})
                                .then(res=>{
                                    this.dataListTrazabilidadExamenes.push(...res.data);
                                    var i=0;
                                    this.dataListTrazabilidadExamenes.map(e=>{
                                    i++
                                    e.index=i;
                                    return e;
                                })
                                this.loading=false
                                }).catch(err=>this.loading=false )
                                }
                  },
                  listarExamenesPendientes(headers){
                    this.axios.get('/listaCompletaDePendientesAdmision',{headers})
                    .then(res=>{
                        this.dataListTrazabilidadExamenes.push(...res.data);
                        var i=0;
                        this.dataListTrazabilidadExamenes.map(e=>{
                            i++
                            e.index=i;
                            return e;
                        })
                     this.loading=false;
                    }).catch(err=>this.loading=false )

                  },
                 convertidorFecha(str){
                        if(str==null){
                            return 'Sin Data'
                        }else{
                            const f = new Date(str);
                            var año=f.getFullYear()
                            var mes=(f.getMonth() + 1)
                                mes=(mes<10)?('0'+mes):mes;
                            var dia=f.getDate();
                                dia=(dia<10)?('0'+dia):dia;
                        return año+'/'+mes+'/'+dia;
                        }
                },
                restarFecha(e){
                    let fecha1 = new Date(e);
                        let fecha2 = new Date()

                        let resta = fecha1.getTime() - fecha2.getTime()
                        return Math.round(resta/ (1000*60*60*24)) +1;
                    },
                //  Clasificador (data){
                //  var Lista=[];
                //  Lista=this.dataListExamenes.filter(ele=>{ if(ele.estado===data){ return ele.estado ; }})
                //   return Lista
                // },
            }         
}
</script>
